import React from 'react';
import { ThemeProvider } from '@material-ui/core';
import { theme } from './theme';
import { Calendar } from './calendar';
import { MenuContext } from './components/header';

export const CardRefContext = React.createContext({
    cardRef: null as HTMLDivElement | null,
    setCardRef: (value: HTMLDivElement) => {},
});

function App() {
    const [menuValue, setMenuValue] = React.useState<
        { x?: number; title?: string | null }[]
    >([]);
    const [cardRef, setCardRef] = React.useState<HTMLDivElement | null>(null);

    React.useEffect(() => {
        const token = localStorage.getItem('esens_token');
    }, []);

    return (
        <CardRefContext.Provider
            value={{
                cardRef,
                setCardRef: (value: HTMLDivElement) => setCardRef(value),
            }}
        >
            <MenuContext.Provider value={{ menuValue, setMenuValue }}>
                <ThemeProvider theme={theme}>
                    <Calendar />
                </ThemeProvider>
            </MenuContext.Provider>
        </CardRefContext.Provider>
    );
}

export default App;
