import { Grow, makeStyles, IconButton } from '@material-ui/core';
import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';

interface HoverablePanelProps {
    isOpen: boolean;
    onClick: () => void;
}

export function HoverablePanel(props: HoverablePanelProps) {
    const classes = useStyles();

    return (
        <Grow in={props.isOpen}>
            <div className={classes.hoverElement}>
                <IconButton onClick={props.onClick}>
                    <AddShoppingCartIcon />
                </IconButton>
            </div>
        </Grow>
    );
}

const useStyles = makeStyles({
    hoverElement: {
        position: 'absolute',
        top: 0,
        bottom: 0,
        right: 0,
        display: 'flex',
        background: '#02B2ED',
        alignItems: 'center',
        '& svg': {
            fill: '#ffffff',
        },
    },
});
