import { pick, reduce, mapValues } from 'lodash';
import { CalendarItem } from './calendar_item';
import { Day } from './day';

export class CalendarScreen {
    private constructor(
        private readonly props: {
            cards: CalendarItem[];
        },
    ) {}

    get cards(): CalendarItem[] {
        return this.props.cards;
    }

    get days(): { [index: string]: Day } {
        return this.props.cards.reduce((acc, card) => {
            const { dayOfTheWeek } = card;

            if (acc[dayOfTheWeek + '']) {
                const day = acc[dayOfTheWeek + ''];
                day.addCard(card);

                return { ...acc, [dayOfTheWeek]: day };
            }

            const day = Day.create(dayOfTheWeek + '', [card]);

            return { ...acc, [dayOfTheWeek]: day };
        }, {} as { [index: string]: Day });
    }

    getSelectedDays(options: { selected: string[]; group_name?: string[] }) {
        if (options.selected.length === 0) return this.days;

        return pick(this.days, options.selected);
    }

    getSuitableSchedule(options: { selected: string[] }) {
        const pickedDays = this.getSelectedDays(options);

        return reduce(
            pickedDays,
            countMinSchedule,
            {} as { [index: string]: number },
        );
    }

    static create(cards: CalendarItem[]): CalendarScreen {
        return new CalendarScreen({ cards });
    }
}

function countMinSchedule(acc: { [index: string]: number }, day: Day) {
    const dailyHours = mapValues(
        day.getCardsByHours(),
        (items) => items.length,
    );

    Object.entries(dailyHours).forEach(([key, value]) => {
        const mainAccHour = acc[key];

        if (!mainAccHour || mainAccHour < value) {
            acc[key] = value;
        }
    });

    return { ...acc };
}
