import React, { useRef } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import FlipMove from 'react-flip-move';
import { Box, Typography } from '@material-ui/core';
import { Day } from './domains/day';
import { MobileCard } from './components/card/mobile_card';
import { CalendarCard } from './components/card/card';
import { EmptyCard } from './components/card/empty_card';

interface Props {
    name: string;
    day: Day;
    noc: number;
    suitableSchable: { [index: string]: number };
    extended?: boolean;
    extend?: boolean;
    isMobile?: boolean;
}

export function Row({
    name,
    noc,
    suitableSchable,
    extended,
    extend,
    day,
    isMobile,
}: Props) {
    const classes = useStyles();
    const ref = useRef<any>(null);
    const cardsByHours = day.getCardsByHours();

    const Card = isMobile ? MobileCard : CalendarCard;

    return (
        <div className={classes.root} ref={ref}>
            <div className={clsx(classes.tagName, 'day-row')}>
                {isMobile ? (
                    <Box mt={3} display='flex' justifyContent='center'>
                        <Typography variant='h2'>{name}</Typography>
                    </Box>
                ) : (
                    name
                )}
            </div>
            <div className='singleRow'>
                <FlipMove>
                    {Object.entries(suitableSchable)
                        .sort(sortByStartTime)
                        .map(([hour, cardsNumber]) => {
                            const myCards = cardsByHours[hour];

                            if (!myCards) {
                                return (
                                    <Box
                                        display='flex'
                                        flexDirection={
                                            extend ? 'row' : 'column'
                                        }
                                        flexWrap='wrap'
                                        key={hour + cardsNumber}
                                        className={hour}
                                    >
                                        {new Array(extended ? cardsNumber : 0)
                                            .fill(null)
                                            .map(() => (
                                                <Box width={mapDaysToSize(noc)}>
                                                    <EmptyCard />
                                                </Box>
                                            ))}
                                    </Box>
                                );
                            }

                            return (
                                <Box
                                    display='flex'
                                    flexDirection={extend ? 'row' : 'column'}
                                    flexWrap='wrap'
                                    key={hour}
                                    className={hour}
                                >
                                    {myCards.map((card, i) => (
                                        <Box
                                            width={
                                                isMobile
                                                    ? 1
                                                    : mapDaysToSize(noc)
                                            }
                                            key={card.id + i}
                                        >
                                            <Card card={card} />
                                        </Box>
                                    ))}
                                    {new Array(
                                        extended
                                            ? cardsNumber - myCards.length
                                            : 0,
                                    )
                                        .fill(null)
                                        .map((_, i) => (
                                            <Box
                                                width={mapDaysToSize(noc)}
                                                key={i}
                                            >
                                                <EmptyCard />
                                            </Box>
                                        ))}
                                </Box>
                            );
                        })}
                </FlipMove>
            </div>
        </div>
    );
}

function mapDaysToSize(daysCount: number) {
    if (daysCount === 6) return 1;
    if (daysCount === 5) return 1;
    if (daysCount === 4) return 1;
    if (daysCount === 3) return 1 / 2;
    if (daysCount === 2) return 1 / 2;
    if (daysCount === 1) return 1 / 4;

    return 1;
}

function sortByStartTime([hourA]: [string, number], [hourB]: [string, number]) {
    const [firstHour, firstHourMinutes] = hourA.split(':');
    const [secondHour, secondHourMinutes] = hourB.split(':');

    if (firstHour > secondHour) return 1;

    if (firstHour === secondHour) {
        return firstHourMinutes > secondHourMinutes ? 1 : -1;
    }

    return -1;
}

const useStyles = makeStyles({
    root: {
        flex: 1,
    },
    tagName: {
        fontSize: 24,
        color: 'rgba(0,0,0,.54)',
        cursor: 'default',
        userSelect: 'none',
        whiteSpace: 'nowrap',
        textOverflow: 'ellipsis',
        overflow: 'hidden',
    },
});
