import React from 'react';
import { TextField } from '@material-ui/core';

interface DateFieldProps {
    label: string;
    value: string;
    setValue: (value: string) => void;
    setIsFocused: (value: boolean) => void;
    isPristine: boolean;
    setIsPristine: (value: boolean) => void;
    isValid: boolean;
    errorMessage?: string;
}

export function DateField(props: DateFieldProps) {
    return (
        <TextField
            type='date'
            variant='outlined'
            fullWidth
            size='small'
            label='data urodzenia'
            value={props.value}
            onChange={(e) => props.setValue(e.target.value)}
            onFocus={() => {
                props.setIsFocused(true);
            }}
            onBlur={() => {
                props.setIsPristine(false);
                props.setIsFocused(false);
            }}
            InputLabelProps={{
                shrink: true,
            }}
            error={!props.isPristine && !props.isValid}
            helperText={
                !props.isPristine && !props.isValid && props.errorMessage
            }
        />
    );
}

export function useDateField(options: {
    initialValue: string;
    label: string;
    validator?: (value: string) => boolean;
    errorMessage?: string;
}) {
    const [value, setValue] = React.useState(options.initialValue);
    const [isFocused, setIsFocused] = React.useState(false);
    const [isPristine, setIsPristine] = React.useState(true);
    const isValid = React.useMemo(
        () => (options.validator ? options.validator(value) : true),
        [value, options],
    );
    return {
        value,
        setValue,
        isFocused,
        setIsFocused,
        isValid,
        isPristine,
        setIsPristine,
        validator: options.validator,
        errorMessage: options.errorMessage,
        label: options.label,
        reset: () => setValue(options.initialValue),
    };
}
