import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { CalendarScreen } from './domains/calendar_screen';
import { AllFilters } from './filters';

interface LegendProps {
    calendar: CalendarScreen;
    filters: AllFilters;
}

export function Legend(props: LegendProps) {
    const classes = useStyles();
    const [hours, setHours] = React.useState<
        Array<{ measurement: DOMRect; name: string }>
    >([]);

    React.useEffect(() => {
        setTimeout(() => {
            setHours(
                [
                    ...Array.from(
                        document.querySelector('.singleRow > *')?.children ||
                            [],
                    ),
                ].map((e) => {
                    const measurement = e.getBoundingClientRect();

                    return {
                        measurement,
                        name: e.classList[2],
                    };
                }),
            );
        }, 1000);
    }, [props.calendar, props.filters.days.length]);

    return (
        <div className={classes.root2}>
            {hours.map((h) => {
                return (
                    <div
                        className={classes.root}
                        style={{ height: h.measurement.height }}
                        key={h.name}
                    >
                        {h.name}
                    </div>
                );
            })}
        </div>
    );
}

const useStyles = makeStyles({
    root: {
        color: 'rgba(0,0,0,.54)',
        marginRight: 8,
        cursor: 'default',
        userSelect: 'none',
    },
    root2: {
        display: 'flex',
        flexDirection: 'column',
        marginTop: 35,
    },
});
