import React from 'react';
import {
    FormControl,
    FormHelperText,
    InputLabel,
    OutlinedInput,
} from '@material-ui/core';

interface TextFieldProps {
    label: string;
    value: string;
    setValue: (value: string) => void;
    setIsFocused: (value: boolean) => void;
    isPristine: boolean;
    setIsPristine: (value: boolean) => void;
    isValid: boolean;
    errorMessage?: string;
    multiline?: boolean;
    inputComponent?: React.ReactNode;
}

export function PhoneTextField(props: TextFieldProps) {
    return (
        <FormControl
            variant='outlined'
            size='small'
            fullWidth
            error={!props.isPristine && !props.isValid}
        >
            <InputLabel htmlFor='phone'>{props.label}</InputLabel>
            <OutlinedInput
                label={props.label}
                value={props.value}
                onChange={(e) => props.setValue(e.target.value)}
                onFocus={() => {
                    props.setIsFocused(true);
                }}
                id='phone'
                onBlur={() => {
                    props.setIsPristine(false);
                    props.setIsFocused(false);
                }}
                inputComponent={props.inputComponent as any}
            />
            <FormHelperText id='phone'>
                {!props.isPristine && !props.isValid && props.errorMessage}
            </FormHelperText>
        </FormControl>
    );
}

export function usePhoneTextField(options: {
    initialValue: string;
    label: string;
    validator?: (value: string) => boolean;
    errorMessage?: string;
    inputComponent?: React.ReactNode;
    multiline?: boolean;
}) {
    const [value, setValue] = React.useState(options.initialValue);
    const [isFocused, setIsFocused] = React.useState(false);
    const [isPristine, setIsPristine] = React.useState(true);
    const isValid = React.useMemo(
        () => (options.validator ? options.validator(value) : true),
        [value, options],
    );
    return {
        value,
        setValue,
        isFocused,
        setIsFocused,
        isValid,
        isPristine,
        setIsPristine,
        inputComponent: options.inputComponent,
        validator: options.validator,
        errorMessage: options.errorMessage,
        label: options.label,
        multiline: options.multiline,
        reset: () => setValue(options.initialValue),
    };
}
