import { DataType } from './operations/queries/get_calendar_schedule';

export function usePossibleFilters(results: DataType[]) {
    const groupName = results.map((r) => r.nazwa_grupy);
    const level = results.map((r) => r.poziom_zaawansowania_nazwa);
    const form = results.map((r) => r.id_forma_ruchu + '');
    const day = results.map((r) => r.id_dzien + '');
    const style = results.map((r) => r.id_styl_taneczny + '');
    const typ = results.map((r) => r.typ);

    return {
        group_name: Array.from(new Set(groupName)),
        level: Array.from(new Set(level)),
        form: Array.from(new Set(form)),
        days: Array.from(new Set(day)),
        styles: Array.from(new Set(style)),
        status: ['otwarte', 'zamkniete'],
        type: Array.from(new Set(typ)),
    };
}
