import React from 'react';
import {
    FormControl,
    FormHelperText,
    InputLabel,
    MenuItem,
    Select,
} from '@material-ui/core';

interface SelectFieldProps {
    label: string;
    options: Array<{ value: string; label: string }>;
    value: string;
    setValue: (value: string) => void;
    setIsFocused: (value: boolean) => void;
    isPristine: boolean;
    setIsPristine: (value: boolean) => void;
    isValid: boolean;
    errorMessage?: string;
}

export function SelectField(props: SelectFieldProps) {
    return (
        <FormControl
            variant='outlined'
            size='small'
            fullWidth
            onFocus={() => {
                props.setIsFocused(true);
            }}
            onBlur={() => {
                props.setIsPristine(false);
                props.setIsFocused(false);
            }}
            error={!props.isPristine && !props.isValid}
        >
            <InputLabel id={props.label}>{props.label}</InputLabel>
            <Select
                value={props.value}
                labelId={props.label}
                label={props.label}
                onChange={(e) => props.setValue(e.target.value as string)}
            >
                {props.options.map(({ value, label }) => (
                    <MenuItem key={value} value={value}>
                        {label}
                    </MenuItem>
                ))}
            </Select>
            <FormHelperText id={props.label}>
                {!props.isPristine && !props.isValid && props.errorMessage}
            </FormHelperText>
        </FormControl>
    );
}

export function useSelectField(options: {
    initialValue: string;
    label: string;
    options: Array<{ value: string; label: string }>;
    validator?: (value: string) => boolean;
    errorMessage?: string;
    inputComponent?: React.ReactNode;
    multiline?: boolean;
}) {
    const [value, setValue] = React.useState(options.initialValue);
    const [isFocused, setIsFocused] = React.useState(false);
    const [isPristine, setIsPristine] = React.useState(true);
    const isValid = React.useMemo(
        () => (options.validator ? options.validator(value) : true),
        [value, options],
    );
    return {
        value,
        setValue,
        isFocused,
        setIsFocused,
        isValid,
        isPristine,
        setIsPristine,
        options: options.options,
        validator: options.validator,
        errorMessage: options.errorMessage,
        label: options.label,
        reset: () => setValue(options.initialValue),
    };
}
