import React from 'react';
import { Grid, Box, Grow } from '@material-ui/core';
import { FilterList as FilterListIcon } from '@material-ui/icons';
import { useRecoilValue, useSetRecoilState } from 'recoil';
import { useIsDevice } from './useBreakpoints';
import { Select } from './components/select';
import { TextButton } from './components/text_button';
import { IconButton } from './components/icon_button';
import {
    allInstructorFiltersState,
    allDayFiltersState,
    allDanceNameFiltersState,
    allDanceAdvanceLevelFiltersState,
    allLocalizationFiltersState,
    allGroupStatusFiltersState,
} from './selectors';
import {
    dayFilterState,
    instructorFilterState,
    danceNameFilterState,
    danceAdvanceLevelFilterState,
    groupStatusFilterState,
    setMainFilter,
    getMainFilter,
    localizationFilterState,
    resetMainFilter,
    isFiltersModalOpenState,
} from './atoms';

import { mapDayIdToString } from './utils/mapDayIdToName';

interface Props {
    defaultFilters: {
        group_name: string[];
        level: string[];
        form: string[];
        days: string[];
        styles: string[];
        status: string[];
        type: string[];
    };
    onChange: (value: AllFilters) => void;
}

export interface AllFilters {
    group_name: string[];
    level: string[];
    form: string[];
    days: string[];
    status: string[];
    styles: string[];
}

export function Filters(props: Props) {
    const [
        isShowMoreFiltersActive,
        setIsShowMoreFiltersActive,
    ] = React.useState(false);
    const allLocalizationFilter = useRecoilValue(allLocalizationFiltersState);
    const setDanceNameFilter = useSetRecoilState(danceNameFilterState);
    const allDanceNameFilters = useRecoilValue(allDanceNameFiltersState);
    const activeLocalizationFilter = useRecoilValue(localizationFilterState);
    const setLocalizationFilter = useSetRecoilState(localizationFilterState);
    const activeDanceNameFilter = useRecoilValue(danceNameFilterState);
    const setInstructorFilter = useSetRecoilState(instructorFilterState);
    const setDanceAdvanceLevelFilter = useSetRecoilState(
        danceAdvanceLevelFilterState,
    );
    const activeDanceAdvanceLevelFilter = useRecoilValue(
        danceAdvanceLevelFilterState,
    );

    const activeInstructorFilter = useRecoilValue(instructorFilterState);
    const allInstructorFilters = useRecoilValue(allInstructorFiltersState);
    const allDanceAdvanceLevelFilters = useRecoilValue(
        allDanceAdvanceLevelFiltersState,
    );
    const allDayFilters = useRecoilValue(allDayFiltersState);
    const setDayFilter = useSetRecoilState(dayFilterState);
    const activeDayFilter = useRecoilValue(dayFilterState);

    const allGroupStatusFilters = useRecoilValue(allGroupStatusFiltersState);
    const setGroupStatusFilter = useSetRecoilState(groupStatusFilterState);
    const activeGroupStatusFilter = useRecoilValue(groupStatusFilterState);

    const isFiltersModalOpen = useRecoilValue(isFiltersModalOpenState);
    const setIsFiltersModalOpen = useSetRecoilState(isFiltersModalOpenState);

    const [selectedFilters] = React.useState<AllFilters>({
        group_name: [],
        level: [],
        form: [],
        days: [],
        status: [],
        styles: [],
    });
    const device = useIsDevice();

    React.useEffect(() => {
        props.onChange(selectedFilters);
    }, [selectedFilters, props]);

    return (
        <Grid container spacing={2} alignItems='center'>
            {device.isMobile && (
                <Grid item xs={12} sm={2}>
                    <Box display='flex' justifyContent='flex-end'>
                        <IconButton
                            icon={<FilterListIcon />}
                            text='Filtruj'
                            onClick={() =>
                                setIsFiltersModalOpen((prev) => ({
                                    ...prev,
                                    isFiltersOpen: !prev.isFiltersOpen,
                                }))
                            }
                        />
                    </Box>
                </Grid>
            )}
            {(isFiltersModalOpen.isFiltersOpen || !device.isMobile) && (
                <>
                    <Grow in>
                        <Grid item xs={12} sm={2}>
                            <Select
                                label='Lokalizacja'
                                values={allLocalizationFilter}
                                value={activeLocalizationFilter.map(
                                    (filter) => filter.name,
                                )}
                                onChange={handleLocalizationFilterChange}
                                onReset={() => {
                                    if (getMainFilter() === 'address') {
                                        resetAllFilters();
                                    } else {
                                        handleLocalizationFilterChange(
                                            [],
                                            false,
                                        );
                                    }
                                }}
                            />
                        </Grid>
                    </Grow>{' '}
                    <Grow in>
                        <Grid item xs={12} sm={2}>
                            <Select
                                label='Zajcia'
                                values={allDanceNameFilters}
                                value={activeDanceNameFilter.map(
                                    (filter) => filter.name,
                                )}
                                onChange={handleDanceNameFilterChange}
                                onReset={() => {
                                    if (getMainFilter() === 'name') {
                                        resetAllFilters();
                                    } else {
                                        handleDanceNameFilterChange([], false);
                                    }
                                }}
                            />
                        </Grid>
                    </Grow>
                    <Grow in>
                        <Grid item xs={12} sm={3} md={3}>
                            <Select
                                label='Instruktor'
                                values={allInstructorFilters}
                                value={activeInstructorFilter.map(
                                    (filter) => filter.name,
                                )}
                                onChange={handleInstructorFilterChange}
                                onReset={() => {
                                    if (getMainFilter() === 'instructor') {
                                        resetAllFilters();
                                    } else {
                                        handleInstructorFilterChange([], false);
                                    }
                                }}
                            />
                        </Grid>
                    </Grow>
                    {/* <Grow in>
                        <Grid item xs={12} sm={2}>
                            <Select
                                label='Forma ruchu'
                                value={selectedFilters.form}
                                values={props.defaultFilters.form}
                                onChange={(value) =>
                                    setSelectedFilters((prev) => ({
                                        ...prev,
                                        form: value,
                                    }))
                                }
                            />
                        </Grid>
                            </Grow> */}
                    {/*  */}
                    {isShowMoreFiltersActive && (
                        <>
                            <Grow in>
                                <Grid item xs={12} sm={3} md={3}>
                                    <Select
                                        label='Poziom zaawansowania'
                                        values={allDanceAdvanceLevelFilters}
                                        value={activeDanceAdvanceLevelFilter.map(
                                            (filter) => filter.name,
                                        )}
                                        onChange={
                                            handleDanceAdvanceLevelFilterChange
                                        }
                                        onReset={() => {
                                            if (
                                                getMainFilter() ===
                                                'advanceLevel'
                                            ) {
                                                resetAllFilters();
                                            } else {
                                                handleDanceAdvanceLevelFilterChange(
                                                    [],
                                                    false,
                                                );
                                            }
                                        }}
                                    />
                                </Grid>
                            </Grow>
                            <Grow in>
                                <Grid item xs={12} sm={3} md={3}>
                                    <Select
                                        label='Status grupy'
                                        values={allGroupStatusFilters}
                                        value={activeGroupStatusFilter.map(
                                            (filter) => filter.name,
                                        )}
                                        onChange={handleGroupStatusChange}
                                        onReset={() => {
                                            if (getMainFilter() === 'isOpen') {
                                                resetAllFilters();
                                            } else {
                                                handleGroupStatusChange(
                                                    [],
                                                    false,
                                                );
                                            }
                                        }}
                                    />
                                </Grid>
                            </Grow>
                            <Grow in>
                                <Grid item xs={12} sm={3} md={2}>
                                    <Select
                                        label='Dzie tygodnia'
                                        values={allDayFilters}
                                        value={activeDayFilter.map(
                                            (filter) => filter.name,
                                        )}
                                        onChange={handleDayFilterChange}
                                        mapOptions={(value) =>
                                            mapDayIdToString(value.name)
                                        }
                                        onReset={() => {
                                            if (
                                                getMainFilter() ===
                                                'dayOfTheWeek'
                                            ) {
                                                resetAllFilters();
                                            } else {
                                                handleDayFilterChange(
                                                    [],
                                                    false,
                                                );
                                            }
                                        }}
                                    />
                                </Grid>
                            </Grow>

                            {/* <Grow in>
                                <Grid item xs={12} sm={2}>
                                    <Select
                                        label='Styl'
                                        value={selectedFilters.styles}
                                        values={props.defaultFilters.styles}
                                        onChange={(value) =>
                                            setSelectedFilters((prev) => ({
                                                ...prev,
                                                styles: value,
                                            }))
                                        }
                                    />
                                </Grid>
                            </Grow>
                            <Grow in>
                                <Grid item xs={12} sm={2}>
                                    <Select
                                        label='Status'
                                        value={selectedFilters.status}
                                        onChange={(value) =>
                                            setSelectedFilters((prev) => ({
                                                ...prev,
                                                status: value,
                                            }))
                                        }
                                        values={props.defaultFilters.status}
                                    />
                                </Grid>
                            </Grow>
                            <Grow in>
                                <Grid item xs={12} sm={2}>
                                    <Select
                                        label='Typ grupy'
                                        value={[]}
                                        onChange={(value) =>
                                            setSelectedFilters((prev) => ({
                                                ...prev,
                                                type: value,
                                            }))
                                        }
                                        values={props.defaultFilters.type}
                                    />
                                </Grid>
                            </Grow> */}
                        </>
                    )}
                    <Grid item xs={12} sm={2}>
                        <TextButton
                            text={
                                isShowMoreFiltersActive
                                    ? 'mniej filtrw'
                                    : 'wicej filtrw'
                            }
                            onClick={() =>
                                setIsShowMoreFiltersActive((props) => !props)
                            }
                        />
                    </Grid>
                </>
            )}
        </Grid>
    );

    function handleLocalizationFilterChange(
        values: string[],
        setFilter: boolean = true,
    ) {
        const found = allLocalizationFilter.filter((filter) =>
            values.includes(filter.name),
        );

        if (found.length === 0 && getMainFilter() === 'address') {
            resetMainFilter();
        }

        if (found) {
            if (!getMainFilter() && setFilter) {
                setMainFilter('address');
            }
            setLocalizationFilter(found);
        }
    }

    function handleDanceNameFilterChange(
        values: string[],
        setFilter: boolean = true,
    ) {
        const found = allDanceNameFilters.filter((filter) =>
            values.includes(filter.name),
        );

        if (found.length === 0 && getMainFilter() === 'name') {
            resetMainFilter();
        }

        if (found) {
            if (!getMainFilter() && setFilter) {
                setMainFilter('name');
            }
            setDanceNameFilter(found);
        }
    }

    function handleInstructorFilterChange(
        values: string[],
        setFilter: boolean = true,
    ) {
        const found = allInstructorFilters.filter((filter) =>
            values.includes(filter.name),
        );

        if (found.length === 0 && getMainFilter() === 'instructor') {
            resetMainFilter();
        }

        if (found) {
            if (!getMainFilter() && setFilter) {
                setMainFilter('instructor');
            }
            setInstructorFilter(found);
        }
    }

    function handleDanceAdvanceLevelFilterChange(
        values: string[],
        setFilter: boolean = true,
    ) {
        const found = allDanceAdvanceLevelFilters.filter((filter) =>
            values.includes(filter.name),
        );

        if (found.length === 0 && getMainFilter() === 'advanceLevel') {
            resetMainFilter();
        }

        if (found) {
            if (!getMainFilter() && setFilter) {
                setMainFilter('advanceLevel');
            }
            setDanceAdvanceLevelFilter(found);
        }
    }

    function handleGroupStatusChange(
        values: string[],
        setFilter: boolean = true,
    ) {
        const found = allGroupStatusFilters.filter((filter) => {
            return values.includes(filter.name);
        });

        if (found.length === 0 && getMainFilter() === 'isOpen') {
            resetMainFilter();
        }

        if (found) {
            if (!getMainFilter() && setFilter) {
                setMainFilter('isOpen');
            }
            setGroupStatusFilter(found);
        }
    }

    function handleDayFilterChange(
        values: string[],
        setFilter: boolean = true,
    ) {
        const found = allDayFilters.filter((filter) =>
            values.includes(filter.name),
        );

        if (found.length === 0 && getMainFilter() === 'dayOfTheWeek') {
            resetMainFilter();
        }

        if (found) {
            if (!getMainFilter() && setFilter) {
                setMainFilter('dayOfTheWeek');
            }
            setDayFilter(found);
        }
    }

    function resetAllFilters() {
        resetMainFilter();
        handleLocalizationFilterChange([], false);
        handleDanceNameFilterChange([], false);
        handleInstructorFilterChange([], false);
        handleDanceAdvanceLevelFilterChange([], false);
        handleGroupStatusChange([], false);
        handleDayFilterChange([], false);
    }
}
