import React from 'react';
import { makeStyles, Theme } from '@material-ui/core/styles';
import { Box, Grid } from '@material-ui/core';
import { StickyContainer } from 'react-sticky';
import FlipMove from 'react-flip-move';
import clsx from 'clsx';
import { Row } from './row';
import { Filters, AllFilters } from './filters';
import { useGetCalendarSchedule } from './operations/queries/get_calendar_schedule';
import { usePossibleFilters } from './use_possible_filters';
import { useCalendarCreator2 } from './use_calendar_creator';
import { mapDayIdToString } from './utils/mapDayIdToName';
import { useIsDevice } from './useBreakpoints';
import { Legend } from './legend';
import { MenuContext, StickyHeader } from './components/header';
import { SkeletonCard } from './components/skeleton_card';
import { useRecoilValue, useSetRecoilState } from 'recoil';
import { filteredCalendarItemsState } from './selectors';
import { EmptyCard } from './components/card/empty_card';
import { DanceBuyDialog } from './dance_buy_dialog/dance_buy_dialog';
import { selectedCardState } from './atoms';

export function Calendar() {
    const classes = useStyles();
    const { results, isLoading } = useGetCalendarSchedule();
    const filters = usePossibleFilters(results);
    const filtredState = useRecoilValue(filteredCalendarItemsState);
    const [selectedFilters, setSelectedFilters] = React.useState<AllFilters>({
        group_name: [],
        level: [],
        form: [],
        days: [],
        status: [],
        styles: [],
    });
    const { calendarScreen } = useCalendarCreator2(filtredState);
    const device = useIsDevice();
    const menuContext = React.useContext(MenuContext);
    const selectedCard = useRecoilValue(selectedCardState);
    const setSelectedCard = useSetRecoilState(selectedCardState);

    const days =
        selectedFilters.days.length === 0
            ? calendarScreen.days
            : calendarScreen.getSelectedDays({
                  selected: selectedFilters.days,
                  group_name: selectedFilters.group_name,
              });

    React.useEffect(() => {
        setTimeout(() => {
            if (!device.isSmallMobile) {
                menuContext.setMenuValue(
                    Object.keys(days).map((d) => ({
                        title: mapDayIdToString(d),
                    })),
                );
            } else {
                const elems = Array.from(
                    document.querySelectorAll('.day-row'),
                ).map((e) => ({
                    x: e.getBoundingClientRect().top,
                    title: e.textContent,
                }));

                menuContext.setMenuValue(elems);
            }
        }, 1000);

        // eslint-disable-next-line
    }, [filtredState, Object.keys(days).length, device.isSmallMobile]);

    return (
        <StickyContainer>
            <StickyHeader />
            <DanceBuyDialog
                isOpen={selectedCard.length > 0}
                onClose={() => setSelectedCard([])}
            />
            <div className={classes.root}>
                <Grid container>
                    <Grid item xs={12}></Grid>
                    <Grid item xs={12}>
                        <Box m={3}>
                            <Filters
                                defaultFilters={filters}
                                onChange={(value) => setSelectedFilters(value)}
                            />
                        </Box>
                    </Grid>
                    <Grid item xs={12}>
                        <Box display='flex'>
                            {!device.isSmallMobile && (
                                <Box minWidth='54px'>
                                    <Legend
                                        calendar={calendarScreen}
                                        filters={selectedFilters}
                                    />
                                </Box>
                            )}
                            <Grid
                                container
                                className={clsx({
                                    [classes.scheduleArea]: !device.isSmallMobile,
                                })}
                            >
                                <FlipMove
                                    style={{
                                        display: 'flex',
                                        flexDirection: device.isSmallMobile
                                            ? 'column'
                                            : 'row',
                                        width: '100%',
                                    }}
                                >
                                    {Object.entries(days).length === 0 &&
                                    isLoading ? (
                                        <Grid item xs={12}>
                                            <Box
                                                display='flex'
                                                flexWrap='wrap'
                                                pl={3}
                                            >
                                                {new Array(
                                                    device.isSmallMobile
                                                        ? 5
                                                        : 17,
                                                )
                                                    .fill(null)
                                                    .map((a, index) => {
                                                        const seed = Math.random();

                                                        if (seed > 0.5)
                                                            return (
                                                                <Box
                                                                    key={index}
                                                                    width={
                                                                        device.isSmallMobile
                                                                            ? 1
                                                                            : 1 /
                                                                              6
                                                                    }
                                                                >
                                                                    <EmptyCard />
                                                                </Box>
                                                            );

                                                        return (
                                                            <Box
                                                                key={index}
                                                                width={
                                                                    device.isSmallMobile
                                                                        ? 1
                                                                        : 1 / 6
                                                                }
                                                            >
                                                                <SkeletonCard />
                                                            </Box>
                                                        );
                                                    })}
                                            </Box>
                                        </Grid>
                                    ) : (
                                        Object.entries(days).map(
                                            ([dayName, day], index, allDays) =>
                                                device.isSmallMobile ? (
                                                    <Grid
                                                        key={dayName}
                                                        item
                                                        xs={12}
                                                    >
                                                        <Row
                                                            name={mapDayIdToString(
                                                                dayName,
                                                            )}
                                                            day={day}
                                                            noc={
                                                                Object.keys(
                                                                    days,
                                                                ).length
                                                            }
                                                            suitableSchable={calendarScreen.getSuitableSchedule(
                                                                {
                                                                    selected:
                                                                        selectedFilters
                                                                            .days
                                                                            .length ===
                                                                        0
                                                                            ? Object.keys(
                                                                                  days,
                                                                              )
                                                                            : selectedFilters.days,
                                                                },
                                                            )}
                                                            isMobile
                                                        />
                                                    </Grid>
                                                ) : (
                                                    <Box
                                                        key={dayName}
                                                        width={
                                                            1 /
                                                            Object.entries(days)
                                                                .length
                                                        }
                                                    >
                                                        <Box
                                                            className={clsx({
                                                                [classes.rowContainer]:
                                                                    allDays.length -
                                                                        1 !==
                                                                    index,
                                                            })}
                                                            pr={1}
                                                            mr={1}
                                                        >
                                                            <Row
                                                                name={mapDayIdToString(
                                                                    dayName,
                                                                )}
                                                                day={day}
                                                                noc={
                                                                    Object.keys(
                                                                        days,
                                                                    ).length
                                                                }
                                                                extended
                                                                extend={
                                                                    Object.keys(
                                                                        days,
                                                                    ).length <=
                                                                    3
                                                                }
                                                                suitableSchable={calendarScreen.getSuitableSchedule(
                                                                    {
                                                                        selected:
                                                                            selectedFilters
                                                                                .days
                                                                                .length ===
                                                                            0
                                                                                ? Object.keys(
                                                                                      days,
                                                                                  )
                                                                                : selectedFilters.days,
                                                                    },
                                                                )}
                                                            />
                                                        </Box>
                                                    </Box>
                                                ),
                                        )
                                    )}
                                </FlipMove>
                            </Grid>
                        </Box>
                    </Grid>
                </Grid>
            </div>
        </StickyContainer>
    );
}

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        display: 'flex',
        height: '100%',
    },
    row: {
        paddingBottom: 24,
    },
    rowContainer: {
        borderRight: `1px solid ${theme.palette.divider}`,
    },
    scheduleArea: {
        width: 'calc(100% - 54px)',
    },
}));
