import { DataType } from '../operations/queries/get_calendar_schedule';

export interface CalendarItemProp {
    id: string;
    name: string;
    instructor: string;
    isOpen: boolean;
    advanceLevel: string;
    dayOfTheWeek: number;
    address: string;
    startAt: string;
    endAt: string;
}

export class CalendarItem {
    private constructor(private readonly prop: CalendarItemProp) {}

    get id() {
        return this.prop.id;
    }

    get isOpen() {
        return this.prop.isOpen;
    }

    get name() {
        return this.prop.name;
    }

    get instructor() {
        return this.prop.instructor;
    }

    get advanceLevel() {
        return this.prop.advanceLevel;
    }

    get dayOfTheWeek() {
        return this.prop.dayOfTheWeek;
    }

    get address() {
        return this.prop.address;
    }

    get startAt() {
        return normalizeHour(this.prop.startAt);
    }

    getPropValue(name: string): string | number | boolean {
        return this.prop[name as keyof CalendarItemProp];
    }

    get endAt() {
        return normalizeHour(this.prop.endAt);
    }

    static createFromRaw(item: Partial<DataType>) {
        return new CalendarItem(normalizeToProps(item));
    }
}

function normalizeToProps(item: Partial<DataType>): CalendarItemProp {
    return {
        id: item.identyfikator_comp ?? '',
        name: item.nazwa_grupy ?? '',
        instructor: item.nauczyciel ?? '',
        isOpen: item.nabor ?? false,
        advanceLevel: item.poziom_zaawansowania_nazwa ?? '',
        dayOfTheWeek: item.id_dzien ?? 1,
        address: item.budynek ?? '',
        startAt: item.czas_poczatek ?? '',
        endAt: item.czas_koniec ?? '',
    };
}

function normalizeHour(simpleDate: string): string {
    const [hour, minutes] = simpleDate.split(':');

    return `${hour}:${minutes}`;
}
