import React from 'react';
import axios from 'axios';
import { useSetRecoilState } from 'recoil';
import { calendarItemsState } from '../../atoms';
import { CalendarItem } from '../../domains/calendar_item';

export interface DataType {
    czy_wyswietlac_na_planie_liczbe_uczestnikow: boolean;
    poziom_zaawansowania: number;
    czy_wyswietlac_na_planie_date_startu: boolean;
    nabor: boolean;
    kategoria_wiekowa_nazwa: string;
    typ: string;
    pozostalo_godzin: number;
    id_alok_cotygod: number;
    nazwa: string;
    id_styl_taneczny: number;
    poziom_zaawansowania_image_url: number;
    ilosc_mezczyz_zapisanych_do_grupy: number | null;
    pierwsze_spotkanie: string;
    dodana: boolean;
    pozostalo_lekcji: number;
    platne: boolean | null;
    ilosc_miejsc: number;
    id_budynek: number;
    identyfikator_comp: string;
    poziom_zaawansowania_nazwa: string;
    liczba_zrealizowanych_zajec: number;
    ilosc_uczestnikow_kobiet_w_grupie: number | null;
    styl_taneczny_link: string;
    data_wazne_do: string;
    czestotliwosc: string;
    czy_aktywna: boolean;
    id_poziom_zaawansowania: number;
    cena: number;
    id_nauczyciel: number;
    stat_rzeczywista_ilosc_alokacji: number;
    czy_zapis_dobor_par: boolean;
    czy_wielu_nauczycieli: boolean;
    ilosc_kobiet_zapisanych_do_grupy: number | null;
    data_wazne_od: string;
    czas_koniec: string;
    id_plan: number;
    ostatnie_spotkanie: string;
    id_system_dydakt: number;
    identyfikator: string;
    odwolane_zajecia_grupy: string;
    id_sala: number;
    najblizsze_spotkanie: string;
    stat_ilosc_uczestnikow: number;
    czas_poczatek: string;
    budynek: string;
    id_grupa: number;
    ilosc_uczestnikow_mezczyz_w_grupie: number | null;
    sala: string;
    id_dzien: number;
    nauczyciel: string;
    id_koniec: number;
    id_poczatek: number;
    nazwa_grupy: string;
    id_forma_ruchu: number;
    opis: string | null;
}

interface RawRequest {
    result: {
        dane: DataType[];
    };
}

export function useGetCalendarSchedule() {
    const [results, setResults] = React.useState<DataType[]>([]);
    const [isLoading, setIsLoading] = React.useState(true);
    const setCalendarItems = useSetRecoilState(calendarItemsState);

    React.useEffect(() => {
        axios
            .get<RawRequest>(`${process.env.REACT_APP_CALENDAR_API}/schedule`)
            .then((response) => {
                setIsLoading(false);
                setResults(response.data.result.dane);
                setCalendarItems(
                    response.data.result.dane.map((d) =>
                        CalendarItem.createFromRaw(d),
                    ),
                );
            });
    }, [setCalendarItems]);

    return {
        results,
        isLoading,
    };
}
