import React from 'react';
import {
    createStyles,
    makeStyles,
    Theme,
    withStyles,
    WithStyles,
} from '@material-ui/core/styles';
import ReCAPTCHA from 'react-google-recaptcha';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import MaskedInput from 'react-text-mask';
import { useRecoilValue } from 'recoil';
import { selectedCardFilterState } from '../selectors';
import { Box, Divider, Grid } from '@material-ui/core';
import { CalendarCard } from '../components/card/card';
import { RegisterFormStepper } from './register_form_stepper';
import {
    minLength,
    validateEmail,
    notEmpty,
} from '../utils/form_validations_utils';
import {
    LabeledTextField,
    useTextField,
} from '../components/labeled_textfield';
import {
    PhoneTextField,
    usePhoneTextField,
} from '../components/phone_textfield';
import { SelectField, useSelectField } from '../components/selectfield';
import { DateField, useDateField } from '../components/datefield';
import { useRegisterUser } from '../operations/mutations/register_user';

const styles = (theme: Theme) =>
    createStyles({
        root: {
            margin: 0,
            padding: theme.spacing(2),
            display: 'flex',
            justifyContent: 'center',
        },
        closeButton: {
            position: 'absolute',
            right: theme.spacing(1),
            top: theme.spacing(1),
            color: theme.palette.grey[500],
        },
    });

export interface DialogTitleProps extends WithStyles<typeof styles> {
    id: string;
    children: React.ReactNode;
    onClose: () => void;
}

const DialogTitle = withStyles(styles)((props: DialogTitleProps) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant='h6'>{children}</Typography>
            {onClose ? (
                <IconButton
                    aria-label='close'
                    className={classes.closeButton}
                    onClick={onClose}
                >
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const DialogContent = withStyles((theme: Theme) => ({
    root: {
        padding: theme.spacing(2),
    },
}))(MuiDialogContent);

const DialogActions = withStyles((theme: Theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(1),
    },
}))(MuiDialogActions);

interface Props {
    isOpen: boolean;
    onClose: () => void;
}

export function DanceBuyDialog(props: Props) {
    const classes = useStyles();
    const [isCaptchaValid, setIsCaptchaValid] = React.useState(false);
    const { isLoading, registerUser } = useRegisterUser();
    const selectedCard = useRecoilValue(selectedCardFilterState);
    const firstnameField = useTextField({
        initialValue: '',
        label: 'imie',
        errorMessage: 'Imie powinno mie co najmniej 2 znaki',
        validator: (value) => minLength(value, 2),
    });
    const lastnameField = useTextField({
        initialValue: '',
        label: 'nazwisko',
        errorMessage: 'Nazwisko powinno mie co najmniej 3 znaki',
        validator: (value) => minLength(value, 3),
    });
    const emailField = useTextField({
        initialValue: '',
        label: 'email',
        errorMessage: 'Nieprawidowy adres email',
        validator: validateEmail,
    });
    const opinionField = useTextField({
        initialValue: '',
        label: 'Skd si o nas dowiedziae?',
        multiline: true,
    });
    const phoneField = usePhoneTextField({
        initialValue: '',
        label: 'telefon',
        inputComponent: TextMaskCustom,
        errorMessage: 'Nieprawidowy numer telefonu',
        validator: (value) => {
            return value
                .split(' ')
                .every((partialValue) => partialValue.trim().length === 3);
        },
    });
    const sexField = useSelectField({
        initialValue: '',
        label: 'pe',
        options: [
            {
                value: 'k',
                label: 'Kobieta',
            },
            {
                value: 'm',
                label: 'Mczyzna',
            },
        ],
        errorMessage: 'Nie wybrano pci',
        validator: notEmpty,
    });
    const dateField = useDateField({
        initialValue: '',
        label: 'data urodzenia',
        errorMessage: 'Data urodzenia nie moe by pusta',
        validator: notEmpty,
    });

    const recaptchaRef: any = React.createRef();

    const isValid =
        firstnameField.isValid &&
        lastnameField.isValid &&
        emailField.isValid &&
        phoneField.isValid &&
        dateField.isValid &&
        sexField.isValid &&
        isCaptchaValid;

    if (!selectedCard) return null;

    return (
        <Dialog
            aria-labelledby='customized-dialog-title'
            open={props.isOpen}
            classes={{ root: classes.root, paper: classes.content }}
            maxWidth='md'
        >
            <DialogTitle id='customized-dialog-title' onClose={handleClose}>
                Rejestracja
            </DialogTitle>
            <DialogContent className={classes.content}>
                <Grid container>
                    <Grid item xs={12} md={4}>
                        <CalendarCard card={selectedCard} viewOnly />
                    </Grid>
                    <Grid item xs={1}>
                        <Box
                            display='flex'
                            justifyContent='center'
                            width='100%'
                            height='100%'
                        >
                            <Divider orientation='vertical' />
                        </Box>
                    </Grid>
                    <Grid item xs={12} md={7}>
                        <Grid container spacing={2}>
                            <Grid item xs={12}>
                                <RegisterFormStepper activeStep={0} />
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <LabeledTextField {...firstnameField} />
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <LabeledTextField {...lastnameField} />
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <DateField {...dateField} />
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <SelectField {...sexField} />
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <LabeledTextField {...emailField} />
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <PhoneTextField {...phoneField} />
                            </Grid>
                            <Grid item xs={12}>
                                <LabeledTextField {...opinionField} />
                            </Grid>
                            <Grid item xs={12}>
                                <form
                                    onSubmit={() => {
                                        recaptchaRef.current.execute();
                                    }}
                                >
                                    <ReCAPTCHA
                                        ref={recaptchaRef}
                                        sitekey='6LdaJGAaAAAAABaqdTOVlAolmsatP0bKQxx1cJs6'
                                        onChange={(value) => {
                                            setIsCaptchaValid(!!value);
                                        }}
                                    />
                                </form>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions>
                <Button
                    autoFocus
                    onClick={handleNextClick}
                    color='primary'
                    disabled={!isValid}
                >
                    Dalej {isLoading && 'ffff'}
                </Button>
            </DialogActions>
        </Dialog>
    );

    function handleClose() {
        props.onClose();
    }

    function handleNextClick() {
        if (!selectedCard) return;

        const createUserDto = {
            item: selectedCard.id,
            email: emailField.value,
            firstname: firstnameField.value,
            lastname: lastnameField.value,
            opinion: opinionField.value,
            phone: phoneField.value,
            sex: sexField.value,
            birthDate: dateField.value,
        };

        registerUser(createUserDto).then((e) => console.log(e));
    }
}

const useStyles = makeStyles({
    root: {
        width: '100%',
        '& .MuiBackdrop-root': {
            background: 'rgba(255,255,255,.7)',
        },
        paddingLeft: 0,
    },
    content: {
        // width: '100%',
    },
});

interface TextMaskCustomProps {
    inputRef: (ref: HTMLInputElement | null) => void;
}

function TextMaskCustom(props: TextMaskCustomProps) {
    const { inputRef, ...other } = props;

    return (
        <MaskedInput
            {...other}
            ref={(ref: any) => {
                inputRef(ref ? ref.inputElement : null);
            }}
            mask={[
                /\d/,
                /\d/,
                /\d/,
                ' ',
                /\d/,
                /\d/,
                /\d/,
                ' ',
                /\d/,
                /\d/,
                /\d/,
            ]}
            placeholderChar={'\u2000'}
            showMask
        />
    );
}
