import { makeStyles, Step, StepLabel, Stepper, Theme } from '@material-ui/core';

interface RegisterFormStepperProps {
    activeStep: number;
}

export function RegisterFormStepper(props: RegisterFormStepperProps) {
    const classes = useStyles();
    const steps = [
        'Rejestracja',
        'Wybr biletu',
        'Patno',
        'Dane szczegowe',
    ];

    return (
        <Stepper
            activeStep={props.activeStep}
            alternativeLabel
            classes={{ root: classes.root }}
        >
            {steps.map((label) => (
                <Step key={label}>
                    <StepLabel>
                        <div>{label}</div>
                    </StepLabel>
                </Step>
            ))}
        </Stepper>
    );
}

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        paddingLeft: 0,
        paddingRight: 0,
        '& svg text, & svg circle': {
            fill: '#fff',
            r: 11,
        },
        '& svg circle': {
            stroke: theme.palette.primary.main,
        },
        '& svg.MuiStepIcon-active circle': {
            fill: theme.palette.primary.main,
        },
        '& svg:not(.MuiStepIcon-active) .MuiStepIcon-text': {
            fill: theme.palette.primary.main,
        },
    },
}));
