import React from 'react';
import { Sticky } from 'react-sticky';
import { useRecoilValue, useSetRecoilState } from 'recoil';
import { Box, Button, Grid, makeStyles } from '@material-ui/core';
import { FilterList as FilterListIcon } from '@material-ui/icons';
import { useIsDevice } from '../useBreakpoints';
import { isFilterActive } from '../selectors';
import { IconButton } from './icon_button';
import { isFiltersModalOpenState } from '../atoms';

export const MenuContext = React.createContext({
    menuValue: [] as { x?: number; title?: string | null }[],
    setMenuValue: (value: { x?: number; title?: string | null }[]) => {},
});

export function StickyHeader() {
    const menuTitles = React.useContext(MenuContext);
    const classes = useStyles();
    const device = useIsDevice();
    const isActiveFilter = useRecoilValue(isFilterActive);
    const setIsFiltersModalOpen = useSetRecoilState(isFiltersModalOpenState);

    if (device.isSmallMobile && isActiveFilter) return null;

    return (
        <Sticky topOffset={100} disableCompensation>
            {({ style, isSticky, distanceFromTop }) => {
                const a = menuTitles.menuValue.map((e) => {
                    return { x: e.x || 0, title: e.title };
                });

                const result = a.filter((f, index, acc) => {
                    const currentScroll = distanceFromTop * -1;
                    const currentElement = acc[index];
                    // const nextElement = acc[index + 1];

                    if (currentScroll < acc[0].x) return true;

                    // if (!nextElement) return true;

                    return currentElement.x < currentScroll;
                });

                const filtredTitles = device.isSmallMobile
                    ? result
                        ? result
                        : []
                    : menuTitles.menuValue;

                return (
                    <header style={{ ...style, zIndex: 1000, marginRight: 8 }}>
                        {isSticky && (
                            <Box display='flex' mr={1}>
                                <Box width='54px'>&nbsp;</Box>
                                <Box
                                    pt={1}
                                    pb={1}
                                    display='flex'
                                    className={classes.container}
                                >
                                    <Grid
                                        container
                                        direction={
                                            device.isSmallMobile
                                                ? 'row'
                                                : 'column'
                                        }
                                    >
                                        <Grid item xs={9} sm={12}>
                                            <Box
                                                display={
                                                    device.isSmallMobile
                                                        ? 'block'
                                                        : 'flex'
                                                }
                                            >
                                                {filtredTitles.map((m) => {
                                                    return device.isSmallMobile ? (
                                                        <Box
                                                            width={
                                                                device.isSmallMobile
                                                                    ? '100%'
                                                                    : 1 /
                                                                      Object.entries(
                                                                          menuTitles,
                                                                      ).length
                                                            }
                                                            className={
                                                                classes.tagName
                                                            }
                                                        >
                                                            <Button
                                                                onClick={() => {
                                                                    window.scrollTo(
                                                                        0,
                                                                        m.x!,
                                                                    );
                                                                }}
                                                            >
                                                                {m.title}
                                                            </Button>
                                                        </Box>
                                                    ) : (
                                                        <Box
                                                            width={
                                                                device.isSmallMobile
                                                                    ? '100%'
                                                                    : 1 /
                                                                      Object.entries(
                                                                          menuTitles,
                                                                      ).length
                                                            }
                                                            className={
                                                                classes.tagName
                                                            }
                                                            onClick={() => {
                                                                window.scrollTo(
                                                                    0,
                                                                    m.x!,
                                                                );
                                                            }}
                                                        >
                                                            {m.title}
                                                        </Box>
                                                    );
                                                })}
                                            </Box>
                                        </Grid>
                                        {device.isSmallMobile && (
                                            <Grid item xs={3}>
                                                <Box mr={1}>
                                                    <IconButton
                                                        icon={
                                                            <FilterListIcon />
                                                        }
                                                        text='Filtruj'
                                                        onClick={() => {
                                                            window.scrollTo(
                                                                0,
                                                                0,
                                                            );
                                                            setIsFiltersModalOpen(
                                                                (prev) => ({
                                                                    ...prev,
                                                                    isFiltersOpen: true,
                                                                }),
                                                            );
                                                        }}
                                                    />
                                                </Box>
                                            </Grid>
                                        )}
                                    </Grid>
                                </Box>
                            </Box>
                        )}
                    </header>
                );
            }}
        </Sticky>
    );
}

const useStyles = makeStyles({
    container: {
        background: '#ffffff',
        boxShadow: '0px 0px 8px 10px #ffffff',
        zIndex: 9999999999,
        width: '100%',
    },
    tagName: {
        fontSize: 24,
        color: 'rgba(0,0,0,.54)',
        cursor: 'PropTypes.instanceOf().isRequired,',
        userSelect: 'none',
        whiteSpace: 'nowrap',
        textOverflow: 'ellipsis',
        overflow: 'hidden',
    },
});
