export function mapDayIdToString(dayId: string) {
    if (dayId === '1') return 'Poniedziaek';
    if (dayId === '2') return 'Wtorek';
    if (dayId === '3') return 'roda';
    if (dayId === '4') return 'Czwartek';
    if (dayId === '5') return 'Pitek';
    if (dayId === '6') return 'Sobota';

    return 'Niedziela';
}
