import React from 'react';
import { makeStyles, Theme } from '@material-ui/core';
import { CardRefContext } from '../../App';

export const EmptyCard = React.memo(function EmptyCard() {
    const cardRefContext = React.useContext(CardRefContext);
    const cardHeight = cardRefContext.cardRef?.clientHeight ?? 100;
    const classes = useStyles({ height: cardHeight });

    return <div className={classes.element}>&nbsp;</div>;
});

const useStyles = makeStyles((theme: Theme) => ({
    element: {
        display: 'flex',
        minHeight: (props: { height: number }) => props.height,
        userSelect: 'none',
        marginBottom: 8,
    },
}));
