import React from 'react';
import { Grid, Card, makeStyles, Typography } from '@material-ui/core';

import { CalendarItem } from '../../domains/calendar_item';
import { CardRefContext } from '../../App';
import { useSetRecoilState } from 'recoil';
import { selectedCardState } from '../../atoms';
import { HoverablePanel } from './hoverable_panel';

interface Props {
    card: CalendarItem;
    viewOnly?: boolean;
}

export const CalendarCard = React.memo(function CalendarCard({
    card,
    viewOnly,
}: Props) {
    const classes = useStyles();
    const cardRef = React.useRef(null);
    const cardRefContext = React.useContext(CardRefContext);
    const [isHovered, setIsHovered] = React.useState(false);
    const setSelectedCard = useSetRecoilState(selectedCardState);

    const [startHour, startMinutes] = card.startAt.split(':');
    const [endHour, endMinutes] = card.endAt.split(':');

    React.useEffect(() => {
        cardRefContext.setCardRef(cardRef.current!);
    }, [cardRefContext]);

    return (
        <Card
            classes={{ root: classes.card }}
            ref={cardRef}
            onMouseOver={handleMouseOverCard}
            onMouseOut={handleMouseOutCard}
        >
            <Grid container spacing={1}>
                <Grid item xs={12}>
                    <Typography
                        variant='h5'
                        title={card.name}
                        className={classes.innerTitle}
                    >
                        {card.name}
                    </Typography>
                </Grid>
                <Grid item xs={12} className={classes.attribute}>
                    <Typography variant='subtitle2'>
                        {card.instructor}
                    </Typography>
                </Grid>
                <Grid item xs={12} className={classes.attribute}>
                    <Typography variant='subtitle2'>
                        {[startHour, startMinutes].join(':')}-
                        {[endHour, endMinutes].join(':')} {card.address}
                    </Typography>
                </Grid>
                <Grid item xs={12} className={classes.attribute}>
                    <Typography variant='subtitle2'>
                        {card.advanceLevel}
                    </Typography>
                </Grid>
            </Grid>
            <HoverablePanel isOpen={isHovered} onClick={handleBuyClick} />
        </Card>
    );

    function handleMouseOverCard() {
        if (!viewOnly && !isHovered) {
            setIsHovered(true);
        }
    }

    function handleMouseOutCard() {
        if (!viewOnly && isHovered) {
            setIsHovered(false);
        }
    }

    function handleBuyClick() {
        setSelectedCard([card.id]);
    }
});

const useStyles = makeStyles({
    card: {
        borderLeft: '3px solid #02B2ED',
        position: 'relative',
        cursor: 'default',
        padding: 8,
        marginBottom: 8,
    },
    innerTitle: {
        marginBottom: 8,
        whiteSpace: 'nowrap',
        textOverflow: 'ellipsis',
        overflow: 'hidden',
    },
    attribute: {
        whiteSpace: 'nowrap',
        textOverflow: 'ellipsis',
        overflow: 'hidden',
    },
});
