import { DataType } from './operations/queries/get_calendar_schedule';
import { CalendarItem } from './domains/calendar_item';
import { CalendarScreen } from './domains/calendar_screen';
import { AllFilters } from './filters';

export function useCalendarCreator(
    calendarData: DataType[],
    filters: AllFilters,
): {
    calendarItems: CalendarItem[];
    sortedByDay: { [index: number]: CalendarItem[] };
    calendarScreen: CalendarScreen;
} {
    const items = calendarData
        .filter(filterItems)
        .map((c) => CalendarItem.createFromRaw(c));
    const sortedByDay = items.reduce((acc, item) => {
        const isRow = acc[item.dayOfTheWeek];
        let row = [];

        if (isRow) {
            row = [...acc[item.dayOfTheWeek]];
            row.push(item);
        } else {
            row = [item];
        }

        return { ...acc, [item.dayOfTheWeek]: row };
    }, {} as { [index: number]: CalendarItem[] });

    const calendarScreen = CalendarScreen.create(items);

    return {
        calendarItems: items,
        sortedByDay,
        calendarScreen,
    };

    function filterItems(item: DataType) {
        let booleanResults: boolean[] = [];

        if (filters.group_name.length > 0) {
            booleanResults.push(filters.group_name.includes(item.nazwa_grupy));
        }

        if (filters.level.length > 0) {
            booleanResults.push(
                filters.level.includes(item.poziom_zaawansowania_nazwa),
            );
        }

        if (filters.form.length > 0) {
            booleanResults.push(
                filters.form.includes(item.id_forma_ruchu + ''),
            );
        }

        if (filters.styles.length > 0) {
            booleanResults.push(
                filters.styles.includes(item.id_styl_taneczny + ''),
            );
        }

        return booleanResults.length > 0
            ? booleanResults.every((e) => e)
            : true;
    }
}

