import { atom } from 'recoil';
import { CalendarItem } from './domains/calendar_item';

export interface FilterItem {
    name: string;
    count: number;
    filtredCount: number;
}

let _mainFilter: string | null = null;

export function getMainFilter(): string | null {
    return _mainFilter;
}

export function setMainFilter(mainFilter: string): void {
    _mainFilter = mainFilter;
}

export function resetMainFilter(): void {
    _mainFilter = null;
}

export const calendarItemsState = atom<CalendarItem[]>({
    key: 'calendar_items',
    default: [],
});

export const selectedCardState = atom<string[]>({
    key: 'selected_card',
    default: [],
});

export const localizationFilterState = atom<FilterItem[]>({
    key: 'localization_filter',
    default: [],
});

export const danceNameFilterState = atom<FilterItem[]>({
    key: 'dance_name_filter',
    default: [],
});

export const instructorFilterState = atom<FilterItem[]>({
    key: 'instructor_filter',
    default: [],
});

export const danceAdvanceLevelFilterState = atom<FilterItem[]>({
    key: 'dance_advance_level_filter',
    default: [],
});

export const dayFilterState = atom<FilterItem[]>({
    key: 'day_filter',
    default: [],
});

export const groupStatusFilterState = atom<FilterItem[]>({
    key: 'group_status_filter',
    default: [],
});

export const minCardMeasurementState = atom<{ height: number }>({
    key: 'min_card_height_state',
    default: {
        height: 100,
    },
});

export const isFiltersModalOpenState = atom<{ isFiltersOpen: boolean }>({
    key: 'is_filters_modal_open_state',
    default: {
        isFiltersOpen: false,
    },
});
