import React from 'react';
import {
    Grid,
    Card,
    makeStyles,
    Typography,
    Box,
    IconButton,
} from '@material-ui/core';
import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import { useSetRecoilState } from 'recoil';
import { CalendarItem } from '../../domains/calendar_item';
import { selectedCardState } from '../../atoms';

interface Props {
    card: CalendarItem;
}

export function MobileCard({ card }: Props) {
    const classes = useStyles();
    const setSelectedCard = useSetRecoilState(selectedCardState);

    const [startHour, startMinutes] = card.startAt.split(':');
    const [endHour, endMinutes] = card.endAt.split(':');

    return (
        <div className={classes.element}>
            <Card className={classes.card}>
                <Grid container direction='row'>
                    <Grid item xs={10}>
                        <Grid container>
                            <Grid item xs={12} className={classes.title}>
                                <div
                                    className={classes.innerTitle}
                                    title={card.name}
                                >
                                    <Typography variant='h5'>
                                        {card.name}
                                    </Typography>
                                </div>
                            </Grid>
                        </Grid>
                        <Grid container justify='space-between'>
                            <Grid item xs={12} className={classes.attribute}>
                                <Typography variant='subtitle2'>
                                    {card.instructor}
                                </Typography>
                            </Grid>
                        </Grid>
                        <Grid container>
                            <Grid item xs={12} className={classes.attribute}>
                                <Typography variant='subtitle2'>
                                    {[startHour, startMinutes].join(':')}-
                                    {[endHour, endMinutes].join(':')}{' '}
                                    {card.address}
                                </Typography>
                            </Grid>
                        </Grid>
                        <Grid container justify='space-between'>
                            <Grid item xs={12} className={classes.attribute}>
                                <Typography variant='subtitle2'>
                                    {card.advanceLevel}
                                </Typography>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={2}>
                        <Box
                            display='flex'
                            justifyContent='center'
                            alignItems='center'
                            width='100%'
                            height='100%'
                        >
                            <IconButton
                                onClick={() => setSelectedCard([card.id])}
                            >
                                <AddShoppingCartIcon />
                            </IconButton>
                        </Box>
                    </Grid>
                </Grid>
            </Card>
        </div>
    );
}

const useStyles = makeStyles({
    root: {
        flex: 1,
    },
    element: {
        padding: '12px 12px 0 0',
        display: 'flex',
        cursor: 'default',
        // minWidth: 200,
    },
    card: {
        borderLeft: '3px solid #02B2ED',
        // padding: `8px 0 0 0`,
        width: '100%',
    },
    title: {
        fontSize: 12,
        marginBottom: 8,
    },
    innerTitle: {
        marginLeft: 8,
        whiteSpace: 'nowrap',
        textOverflow: 'ellipsis',
        overflow: 'hidden',
        // height: 30,
    },
    attribute: {
        marginBottom: 8,
        marginLeft: 8,
        whiteSpace: 'nowrap',
        textOverflow: 'ellipsis',
        overflow: 'hidden',
    },
    button: {
        color: '#1976D2',
        fontSize: 9,
    },
    tagName: {
        fontSize: 24,
        color: 'rgba(0,0,0,.54)',
        cursor: 'default',
        userSelect: 'none',
        height: 30,
    },
});
