import { GetRecoilValue, selector } from 'recoil';
import {
    FilterItem,
    getMainFilter,
    instructorFilterState,
    selectedCardState,
} from './atoms';
import { CalendarItem } from './domains/calendar_item';
import { dayFilterState, groupStatusFilterState } from './atoms';
import {
    localizationFilterState,
    danceNameFilterState,
    calendarItemsState,
    danceAdvanceLevelFilterState,
} from './atoms';

export const filteredCalendarItemsState = selector({
    key: 'filteredTodoListState',
    get: ({ get }) => {
        const danceNameFilter = get(danceNameFilterState);
        const localizationFilter = get(localizationFilterState);
        const danceAdvanceLevelFilter = get(danceAdvanceLevelFilterState);
        const instructorFilter = get(instructorFilterState);
        const dayFilter = get(dayFilterState);
        const groupStatusFilter = get(groupStatusFilterState);
        const calendarItems = get(calendarItemsState);

        return calendarItems.filter((item) => {
            const hasLocalization =
                localizationFilter.some(
                    (filterItem) => filterItem.name === item.address,
                ) ?? false;

            const hasDanceName =
                danceNameFilter.some(
                    (filterItem) => filterItem.name === item.name,
                ) ?? false;
            const hasInstructor =
                instructorFilter.some(
                    (filterItem) => filterItem.name === item.instructor,
                ) ?? false;
            const hasAdvanceLevel =
                danceAdvanceLevelFilter.some(
                    (filterItem) => filterItem.name === item.advanceLevel,
                ) ?? false;

            const hasDay =
                dayFilter.some(
                    (filterItem) =>
                        filterItem.name === item.dayOfTheWeek.toString(),
                ) ?? false;

            const hasGroupStatus =
                groupStatusFilter.some(
                    (filterItem) =>
                        filterItem.name ===
                        (item.isOpen ? 'otwarte' : 'zamknite'),
                ) ?? false;

            let decisionList: boolean[] = [];

            if (localizationFilter.length > 0)
                decisionList.push(hasLocalization);
            if (danceNameFilter.length > 0) decisionList.push(hasDanceName);
            if (instructorFilter.length > 0) decisionList.push(hasInstructor);
            if (danceAdvanceLevelFilter.length > 0)
                decisionList.push(hasAdvanceLevel);
            if (groupStatusFilter.length > 0) decisionList.push(hasGroupStatus);
            if (dayFilter.length > 0) decisionList.push(hasDay);

            return decisionList.length > 0
                ? decisionList.every((decision) => decision)
                : true;
        });
    },
});

export const allLocalizationFiltersState = selector<FilterItem[]>({
    key: 'all_localization_filters',
    get: ({ get }) => {
        const calendarItems = get(calendarItemsState);
        const mainFilter = getMainFilter();

        return Array.from(new Set(calendarItems.map((item) => item.address)))
            .map((name) => {
                const count = calendarItems.filter(
                    (item) => item.address === name,
                ).length;

                const filtredCount = filterWithMainFilter({ get }).filter(
                    (item) => {
                        return item.address === name;
                    },
                ).length;

                return {
                    name,
                    count,
                    filtredCount,
                };
            })
            .filter((filter) => {
                if (!filter || mainFilter === 'address') return true;

                return filter.filtredCount > 0;
            });
    },
});

