function filterWithMainFilter({
    get,
}: {
    get: GetRecoilValue;
}): CalendarItem[] {
    const mainFilter = getMainFilter();
    const calendarItems = get(calendarItemsState);
    const localizationFilter = get(localizationFilterState);
    const danceNameFilter = get(danceNameFilterState);
    const danceAdvanceLevelFilter = get(danceAdvanceLevelFilterState);
    const groupStatusFilter = get(groupStatusFilterState);
    const instructorFilter = get(instructorFilterState);
    const dayFilter = get(dayFilterState);

    const filtredItems = calendarItems.filter((item) => {
        if (mainFilter === 'address') {
            const hasDanceAddress = localizationFilter.find(
                (filter) => filter.name === item.getPropValue(mainFilter),
            );

            return hasDanceAddress;
        }

        if (mainFilter === 'name') {
            const hasDanceName = danceNameFilter.find(
                (filter) => filter.name === item.getPropValue(mainFilter),
            );

            return hasDanceName;
        }

        if (mainFilter === 'instructor') {
            const hasInstructor = instructorFilter.find(
                (filter) => filter.name === item.getPropValue(mainFilter),
            );

            return hasInstructor;
        }

        if (mainFilter === 'advanceLevel') {
            const hasDanceAdvanceLevel = danceAdvanceLevelFilter.find(
                (filter) => filter.name === item.getPropValue(mainFilter),
            );

            return hasDanceAdvanceLevel;
        }

        if (mainFilter === 'isOpen') {
            const hasGroupStatusLevel = groupStatusFilter.find(
                (filter) =>
                    filter.name ===
                    (item.getPropValue(mainFilter) ? 'otwarte' : 'zamknite'),
            );

            return hasGroupStatusLevel;
        }

        if (mainFilter === 'dayOfTheWeek') {
            const hasDay = dayFilter.find(
                (filter) =>
                    filter.name === item.getPropValue(mainFilter).toString(),
            );

            return hasDay;
        }

        return true;
    });

    return filtredItems;
}

export const selectedCardFilterState = selector<CalendarItem | undefined>({
    key: 'selected_card_filters',
    get: ({ get }) => {
        const calendarItems = get(calendarItemsState);
        const selectedCardsIds = get(selectedCardState);

        return calendarItems.find((item) => item.id === selectedCardsIds[0]);
    },
});

export const isFilterActive = selector<boolean>({
    key: 'is_filter_active',
    get: ({ get }) => {
        const activeLocalizationFilters = get(localizationFilterState);
        const activeDanceNameFilters = get(danceNameFilterState);
        const activeInstructorFilters = get(instructorFilterState);
        const activeDanceAdvanceLevelFilters = get(
            danceAdvanceLevelFilterState,
        );
        const activeDayFilters = get(dayFilterState);
        const activeGroupStatusFilters = get(groupStatusFilterState);

        return (
            activeLocalizationFilters.length > 0 ||
            activeDanceNameFilters.length > 0 ||
            activeInstructorFilters.length > 0 ||
            activeDanceAdvanceLevelFilters.length > 0 ||
            activeDayFilters.length > 0 ||
            activeGroupStatusFilters.length > 0
        );
    },
});
