import { CalendarItem } from './calendar_item';
import { AllFilters } from '../filters';

export class Day {
    private _filters: Omit<AllFilters, 'days'> = {
        group_name: [],
        level: [],
        form: [],
        status: [],
        styles: [],
    };

    private constructor(
        private readonly props: { name: string; cards: CalendarItem[] },
    ) {}

    get name(): string {
        return this.props.name;
    }

    get cards(): CalendarItem[] {
        return this.props.cards;
    }

    isEmpty(): boolean {
        return this.props.cards.length === 0;
    }

    addCard(card: CalendarItem): void {
        this.props.cards.push(card);
    }

    select({ group_name }: { group_name?: string[] }): Day {
        const filtredCards = this.props.cards.filter((c) => {
            if (group_name && group_name.length > 0) {
                return group_name.includes(c.name);
            }

            return true;
        });

        return Day.create(this.props.name, filtredCards);
    }

    get filters(): Omit<AllFilters, 'days'> {
        return this._filters;
    }

    setFilters(filters: AllFilters) {
        this._filters = filters;
    }

    getCardsByHours() {
        return this.props.cards.reduce((acc, card) => {
            const { startAt } = card;

            if (acc[startAt]) {
                return { ...acc, [startAt]: [...acc[startAt], card] };
            }

            return { ...acc, [startAt]: [card] };
        }, {} as { [index: string]: CalendarItem[] });
    }

    static create(name: string, cards: CalendarItem[]): Day {
        return new Day({ name, cards });
    }
}
