import React from 'react';
import {
    MenuItem,
    Select as MultiSelect,
    InputLabel,
    FormControl,
    IconButton,
    Typography,
    makeStyles,
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { FilterItem } from '../atoms';

interface Props<T> {
    label: string;
    value: string[];
    values: T[];
    onChange: (value: string[]) => void;
    onReset: () => void;
    mapOptions?: (value: T) => string;
}

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 6 + ITEM_PADDING_TOP,
            width: 250,
        },
    },
};

export function Select<T extends FilterItem>(props: Props<T>) {
    const [isOpen, setIsOpen] = React.useState(false);

    const classes = useStyles();

    return (
        <FormControl variant='outlined' fullWidth>
            <InputLabel id={props.label}>{props.label}</InputLabel>
            <MultiSelect
                labelId={props.label}
                fullWidth
                open={isOpen}
                onClick={() => setIsOpen(true)}
                onClose={(e) => {
                    e.preventDefault();
                    e.stopPropagation();

                    setIsOpen(false);
                }}
                renderValue={(value) => {
                    const filtersCount = (value as string[]).length;

                    if (filtersCount === 1)
                        return (
                            <div>
                                {filtersCount} filtr{' '}
                                <CancelButton
                                    onClick={() => {
                                        // resetMainFilter();
                                        props.onReset();
                                    }}
                                />
                            </div>
                        );
                    if (filtersCount >= 2 || filtersCount <= 4)
                        return (
                            <div>
                                {filtersCount} filtry
                                <CancelButton
                                    onClick={() => {
                                        // resetMainFilter();
                                        props.onReset();
                                    }}
                                />
                            </div>
                        );

                    return (
                        <div>
                            {filtersCount} filtrw{' '}
                            <CancelButton
                                onClick={() => {
                                    // resetMainFilter();
                                    props.onReset();
                                }}
                            />
                        </div>
                    );
                }}
                label={props.label}
                onChange={({ target: { value } }) => {
                    props.onChange(value as string[]);
                }}
                value={props.value}
                multiple
                MenuProps={MenuProps}
            >
                {(
                    [...(props.values || [])].sort(
                        (a, b) => b.filtredCount - a.filtredCount,
                    ) || []
                )
                    // .filter((v) => v.filtredCount !== 0)
                    .sort((a, b) => (a.filtredCount > b.filtredCount ? 1 : -1))
                    .map((v) => (
                        <MenuItem
                            key={Math.random()}
                            value={v.name}
                            // disabled={v.filtredCount === 0}
                        >
                            <Typography
                                variant='overline'
                                className={classes.option}
                            >
                                {props.mapOptions
                                    ? props.mapOptions(v)
                                    : v.name}
                                {/* &nbsp;
                                {v.filtredCount !== 0
                                    ? `(${v.filtredCount} / ${v.count})`
                                    : ''} */}
                                {/* {v.filtredCount !== 0
                                    ? v.filtredCount + ' /'
                                    : ''}
                                {'- '}
                                {v.count}) */}
                            </Typography>
                        </MenuItem>
                    ))}
            </MultiSelect>
        </FormControl>
    );
}

function CancelButton({ onClick }: { onClick: () => void }) {
    return (
        <span
            onClick={(e) => {
                e.stopPropagation();

                onClick();
            }}
        >
            <IconButton size='small'>
                <CloseIcon fontSize='small' />
            </IconButton>
        </span>
    );
}

const useStyles = makeStyles({
    option: {
        whiteSpace: 'nowrap',
        overflow: 'hidden',
        textOverflow: 'ellipsis',
    },
});
