import React from 'react';
import axios from 'axios';

export interface RegisterUserInput {
    item: string;
    email: string;
    firstname: string;
    lastname: string;
    opinion: string;
    phone: string;
    sex: string;
    birthDate: string;
}

export interface RegisterUser {
    token: string;
}

export function useRegisterUser() {
    const [results, setResults] = React.useState<RegisterUser | null>(null);
    const [isLoading, setIsLoading] = React.useState(false);

    return {
        results,
        isLoading,
        registerUser: (value: RegisterUserInput) => {
            setIsLoading(true);

            return axios
                .post<RegisterUser>(
                    `${process.env.REACT_APP_CALENDAR_API}/register`,
                    value,
                )
                .then((response) => {
                    setIsLoading(false);
                    setResults(response.data);

                    console.log(response);
                    localStorage.setItem('esens_token', response.data.token);

                    return response.data;
                });
        },
    };
}
